import React, { Component } from "react";
import navbar from "./_.module.css";
import { MenuList, SocialList } from "../menu";

export default class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      slideMenu: -400,
    };
  }
  callSlide = () => {
    this.setState({
      slideMenu: 0,
    });
  };
  closeSlide = () => {
    this.setState({
      slideMenu: -400,
    });
  };
  render() {
    return (
      <div>
        <nav className={navbar.Navbar}>
          <div className={navbar.Logo}>Teambies</div>
          <ul>
            <SocialList classList="fab fa-instagram" menu="social" link="#" />
            <SocialList classList="fab fa-facebook-f" menu="social" link="#" />
            <SocialList classList="fab fa-twitter" menu="social" link="#" />
            <SocialList
              classList="fas fa-bars"
              menu="bars"
              link="#"
              onClick={this.callSlide}
            />
          </ul>
        </nav>
        <div
          className={navbar.slideMenu}
          style={{ right: this.state.slideMenu + "px" }}
        >
          <div className={navbar.LogoNav}>Teambies</div>
          <ul>
            {this.props.menu.map((el, index) => (
              <MenuList key={index} menu={el} link="#" />
            ))}
            <span className={navbar.exit}>
              <a href="#" onClick={this.closeSlide}>
                <i className="far fa-times-circle"></i>
              </a>
            </span>
          </ul>
        </div>
      </div>
    );
  }
}
