import React from "react";
import menu from "./_.module.css";
export const SocialList = (props) => {
  return (
    <li className={menu[props.menu]}>
      <a href={props.link} onClick={props.onClick}>
        <i className={props.classList}></i>
      </a>
    </li>
  );
};
export const MenuList = (props) => {
  return (
    <li className={props.menu}>
      <a href={props.link} onClick={props.onClick}>
        {props.menu}
      </a>
    </li>
  );
};
