import React from "react";
import css from "./_.module.css";

const Content = (props) => {
  return (
    <div id="content" className={css["content" + props.img]}>
      <div id="dot" className={css["background" + (props.img % 2)]}></div>
      <img
        src={"./img/main" + props.img + ".png"}
        width={props.size + "px"}
      ></img>
      <span className={css.date}>{props.date}</span>
      <span className={css.title}>{props.title}</span>
    </div>
  );
};
export default Content;
