import React from "react";
import css from "./_.module.css";
import Content from "./content";

const Main = (props) => {
  return (
    <div className={css.main}>
      <span className={css.title}>
        "In Photography there is a reality so subtlethat it becomes more{" "}
        <strong className={css.real}>real</strong> than reality"
      </span>
      <div>
        {props.content.map((el, index) => (
          <Content
            key={index}
            title={el.title}
            date={el.date}
            img={el.img}
            size={el.width}
          />
        ))}
      </div>
    </div>
  );
};
export default Main;
