import React, { useState } from "react";
import css from "./_.module.css";
const Content = (props) => {
  const [slide, setSlide] = useState(props.slide);
  return (
    <div
      className={css.show}
      style={{ backgroundImage: `url(./img/${props.img}.jpg)` }}
    >
      <div className={css.content}>
        <h4>{props.title}</h4>
        <span style={{ display: props.text ? "block" : "none" }}>
          {props.text}
        </span>
        <div className={css.slider} style={{ opacity: slide ? 1 : 0 }}>
          <div className={css.selected}></div>
          <div></div>
          <div></div>
        </div>
      </div>
    </div>
  );
};
export default Content;
