import React from "react";
import Content from "./content";
import css from "./_.module.css";
const ShowCase = (props) => {
  return (
    <div className={css.case}>
      {props.content.map((el) => (
        <Content
          key={el.img}
          img={el.img}
          title={el.title}
          text={el.text}
          slide={el.slide}
        />
      ))}
    </div>
  );
};
export default ShowCase;
